<template>
    <canvas width="600" height="600" id="canvas" style="border: 1px solid #ccc;"></canvas>
  </template>
   
  <script setup>
  import { onMounted } from 'vue'
  import { fabric } from 'fabric'
   
  function init() {
    let canvas = new fabric.Canvas('canvas') // fabriccanvas
   
    // 
    let circle = new fabric.Circle({
      left: 100,
      top: 100,
      radius: 50,
    })
   
    // 
    let gradient = new fabric.Gradient({
      type: 'linear', // linear or radial
      gradientUnits: 'pixels', // pixels or pencentage   
      coords: { x1: 0, y1: 0, x2: circle.width, y2: 0 }, // 2(x1y1x2y2)
      colorStops:[ // 
        { offset: 0, color: 'red' },
        { offset: 0.2, color: 'orange' },
        { offset: 0.4, color: 'yellow' },
        { offset: 0.6, color: 'green' },
        { offset: 0.8, color: 'blue' },
        { offset: 1, color: 'purple' },
      ]
    })
    circle.set('fill', gradient);
    canvas.add(circle)
  }
   
  onMounted(() => {
    init()
  })
  </script>